import React, { Component } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import logo from './logo.svg';
import './App.css';
import InstagramIcon from './components/InstagramIcon.js';
import FacebookIcon from './components/FacebookIcon.js';
import CallIcon from '@material-ui/icons/Call';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import test from './assets/test.png';
import Paper from '@material-ui/core/Paper';



class App extends Component {
  render() {
    return (
      <div className="App">
        <header>
          <AppBar position="static"  style={{ backgroundColor: '#43A047'}}>
            <Toolbar>
              <div>
                <IconButton aria-haspopup="true" color="inherit" aria-label="Menu">
                  <FacebookIcon />
                </IconButton>

                <IconButton aria-haspopup="true" color="inherit" aria-label="Menu">
                  <InstagramIcon />
                </IconButton>

                <IconButton aria-haspopup="true" color="inherit" aria-label="Menu">
                  <CallIcon />
                </IconButton>

              </div>
            </Toolbar>
          </AppBar>
        </header>

        <main>
          <div className={styles.root}>
      <Grid container spacing={2}>
        <Grid item xs={12} sm={4}>
          <Avatar alt="Test" src={test} style={styles.bigAvatar}/>
        </Grid>
        <Grid item xs={12} sm={4}>
          <Avatar alt="Test" src={test} style={styles.bigAvatar}/>
        </Grid>
        <Grid item xs={12} sm={4}>
          <Avatar alt="Test" src={test} style={styles.bigAvatar}/>
        </Grid>
      </Grid>
    </div>
        </main>
        <p className="App-intro">
          Estamos creando nuestro sitio web.
        </p>
      </div>
    );
  }
}

const styles = {
  root: {
    flexGrow: 1,
  },
  flex: {
    flex: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
  avatar: {
   margin: 10,
 },
 bigAvatar: {
   width: 300,
   height: 300,
 },
 row: {
    display: 'flex',
    justifyContent: 'center',
  },
   paper: {
     textAlign: 'center',
   },
};

export default App;



